<div class="w-auto h-[100vh] border-r-2"><div class="w-auto  p-2"><el-button class="w-full" :icon="Switch" @click="isCollapse = !isCollapse"></el-button></div><el-menu :default-active="defaultActive" :collapse="isCollapse" background-color="#ffffff" @open="handleOpen" @close="handleClose" style="border-right: 0px;"><el-menu-item v-for="(item, index) of nowMenu" :key="index" :index="index + &quot;&quot;" @click="toLink(item)"><el-icon><component :is="item.icon"></component></el-icon><template #title>{{item.name}}</template></el-menu-item></el-menu></div>